@import '../../utils/breakpoints';

.search {
  height: 40px;
  position: relative;
  background: rgba(25, 59, 103, 0.05);
  &__icon {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 10px;
  }
  &__input {
    display: block;
    height: 100%;
    width: 100%;
    border: none;
    background: rgba(25, 59, 103, 0.05);
    border-radius: 4px;
    padding: 4px 30px;
  }
}

@media (max-width: $mobile-width) {
  .search {
    width: 100%;
    &__input {
      background: white;
      border: 1px solid rgba(28, 55, 90, 0.16);
    }
  }
}