
@keyframes changeWidth {
  from {
    width: 6px;
  }
  to {
    width: 30px;
  }
}

.promo {
  position: relative;
  width: 100%;
  // background: linear-gradient(0deg, rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.2)), url(../../img/promo/promo.png) no-repeat;
  // background-size: cover;
  overflow: hidden;
  &__slider {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    position: absolute;
    overflow: hidden;
    z-index: -1;
  }

  &__slide {
    position: absolute;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: opacity ease-in-out 0.4s;
    img {
      z-index: -1;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    &-active {
      opacity: 1;
    }
  }
  

  &__title {
    font-size: 28px;
    line-height: 125.3%;
    color: #192434;
    width: 60%;
  }
  &__subtitle {
    font-size: 16px;
    line-height: 161.1%;
    color: #192434;
  }
  &__btn {
    height: 40px;
    margin-top: 16px;
    width: 151.11px;
    font-size: 16px;
    line-height: 19px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 12px;
  }
  &__next {
    position: absolute;
    top: 50%;
    right: 0;
    margin-right: 24px;
    width: 32px;
    height: 32px; 
    background: #FFFFFF;
    border-radius: 50%;
    transform: translateY(-50%);
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
  }
  &__pagination {
    position: absolute;
    bottom: 0;
    margin: 24px auto;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    justify-content: center;
  }
  .pagination {
    &__item {
      width: 6px;
      height: 6px;          
      background: #192434;
      border-radius: 50%;
      opacity: 0.5;
      margin-right: 4px;
      cursor: pointer;
      &-active {
        width: 30px;
        border-radius: 21px;
        opacity: 1;
        animation: changeWidth 0.5s forwards;
      }
    }
  }
}