@import '../../utils/breakpoints';

.header {
  display: none;
  justify-content: flex-start;
  align-items: center;
  &__backIcon {
    margin-right: 8px;
    width: 32px;
    height: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__title {
    font-size: 24px;
    line-height: 125.3%;
    color: #192434;
  }
}

@media (max-width: $mobile-width) { 
  .header {
    padding-left: 20px;
    padding-right: 20px;
    display: flex;
    margin-bottom: 24px;
  }
}