@import '../../utils/breakpoints';

.archive {
  display: grid;
  grid-template-columns: minmax(400px, auto) minmax(200px, 262px);
  grid-template-rows: auto-fit;
  column-gap: 16px;
  height: 80%;
  &__page {
    padding: 16px;
  }
  &__header {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    min-height: 40px;
    flex-wrap: wrap;
    row-gap: 8px;
  }
  &__title {
    font-weight: 500;
    font-size: 22px;
    
    line-height: 200%;
    color: #192434;
  }
  &__search {
    margin-left: auto;
    margin-right: 8px;
  }
  &__filtersTitle {
    display: none;
    font-size: 16px;
    line-height: 125.3%;
    color: #192434;
  }
  &__filters {
    display: flex;
    column-gap: 8px;
    .filter {
      &__icon {
        border-radius: 4px;
        width: 40px;
        height: 40px;
        display: flex;
        justify-content: center;
        align-items: center;
        background: rgba(25, 59, 103, 0.05);
        &:hover {
          background: rgba(28, 52, 84, 0.26);
        }
      }
    }
  }
  &__filter {
    position:relative;
  }
  &__transactions {
    margin-top: 4px;
    min-height: 400px;
  }
  .promo {
    background: url(../../img/promo/promo3.png) no-repeat;
    &__content {
      padding: 16px;
    }
    &__title {
      font-size: 22px;
      line-height: 125.3%;      
      color: #FFFFFF;     
      width: 100%; 
    }
    &__btn {
      background: none;
      border: 1px solid;
      width: 110px;
      height: 40px;
      padding: 0;
      margin-top: 16px;
    }
  }
  
}

@media (max-width: 1050px) { 
  .archive {
    grid-template-columns: auto;
    .promo {
      display: none;
    }
  }
}

@media (max-width: 750px) { 
  .archive {
    &__title {
      display: none;
    }
    &__search {
      margin-left: 0;
      margin-right: 0;
    }
  }
}

@media (max-width: $mobile-width) { 
  .archive {
    // animation: openPageTranslate ease-in-out 0.6s forwards;
    position: static;
    &__title {
      display: none;
    }
    &__search {
      margin-left: 0;
    }
    &__filters {
      margin-left: auto;
    }
    &__filtersTitle {
      display: block;
      margin-top: 9px;
    }
    &__filter {
      position: static;
    }
  }
}