@import '../../../utils/breakpoints';

.filterByStatus {
  z-index: 1000;
  position: absolute;
  top: 0;
  right: 0;
  transform: translateY(50px);
  width: 372px;
  height: 457px;
  background: #FFFFFF;
  border: 1px solid rgba(26, 56, 96, 0.1);
  box-shadow: 0px 4px 15px rgba(189, 194, 199, 0.4);
  border-radius: 10px;
  padding: 16px;
  padding-top: 24px;
  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__close {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 16px;
    height: 16px;
  }
  &__title {
    font-size: 24px;
    line-height: 125.3%;
    color: #192434;
  }
  &__choice {
    margin-top: 16px;
  }
  &__points {
    margin-top: 10px;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(30px, auto));
    column-gap: 4px;
    &-nomargin {
      margin: 0;
    }
  }
  &__point {
    background: rgba(28, 52, 84, 0.26);
    border-radius: 64px;
    height: 40px;
    font-size: 16px;
    line-height: 19px;
    color: #FFFFFF;
    &:hover {
      background: #1E4DFC;
    }
  }
  &__clear {
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    color: rgba(28, 52, 84, 0.26);
    margin-top: 15px;
    transition: all 0.5s;
    background: none;
    padding: 0;
    height: 20px;
    color: red;
    &:hover {
      transform: scale(1.02);
      color: #1E4DFC;
    }
  }
  &__buttons {
    position: absolute;
    bottom: 0;
    margin-bottom: 20px;
    width: 100%;
    padding-right: 40px;
  }
}

@media (max-width: $mobile-width) {
  .filterByStatus {
    padding: 20px;
    padding-top: 40px;
    position: fixed;
    transform: translateY(0);
    width: 100%;
    height: 100%;
    border-radius: 0;
    img {
      transform: scale(1.3);
    }
  }
}