@import '../../../utils/breakpoints';

.sortByCategory {
  z-index: 1000;
  position: absolute;
  top: 0;
  right: 0;
  transform: translateY(50px);
  width: 299px;
  height: 232px;
  background: #FFFFFF;
  border: 1px solid rgba(26, 56, 96, 0.1);
  box-shadow: 0px 4px 15px rgba(189, 194, 199, 0.4);
  border-radius: 10px;
  padding: 16px;
  padding-top: 24px;
  &__header {
    display: none;
    justify-content: space-between;
    align-items: center;
  }
  &__close {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 32px;
    height: 32px;
  }
  &__title {
    font-size: 24px;
    line-height: 125.3%;
    color: #192434;
  }
  &__variants {
    display: flex;
    flex-direction: column;
    row-gap: 4px;
  }
  &__variant {
    display: flex;
    align-items: center;
    font-size: 22px;
    line-height: 161.1%;
    color: rgba(24, 39, 58, 0.94);
    column-gap: 10px;
  }
  &__input {
    height: 20px;
    width: 20px;
    appearance: none;
    border: 3px solid rgba(27, 43, 65, 0.72);
    border-radius: 4px;
    outline: none;
    transition-duration: 0.3s;
    cursor: pointer;
    &:checked {
      border: 3px solid #1E4DFC;
      &::before {
        content: '\2714';
        font-size: 10px;
        font-weight: 800;
        display: block;
        text-align: center;
        color: #1E4DFC;
      }
    }
  }
  &__apply {
    display: none;
  }
  &__clear {
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
    color: rgba(28, 52, 84, 0.26);
    margin-top: 15px;
    transition: all 0.5s;
    background: none;
    padding: 0;
    height: 20px;
    &:hover {
      transform: scale(1.02);
      color: #1E4DFC;
    }
  }
}

@media (max-width: $mobile-width) {
  .sortByCategory {
    padding: 20px;
    padding-top: 40px;
    position: fixed;
    transform: translateY(0);
    width: 100%;
    height: 100%;
    border-radius: 0;
    &__header {
      display: flex;
    }
    &__variants {
      margin-top: 36px;
      row-gap: 16px;
    }
    &__variant {
      column-gap: 14px;
    }
    &__input {
      height: 25px;
      width: 25px;
      &:checked {
        &::before {
          font-size: 14px;
        }
      }
    }
    &__apply {
      padding-top: 12px;
      display: block;
    }
    &__clear {
      color: red;
    }
    img {
      transform: scale(1.3);
    }
    &__buttons {
      position: absolute;
      bottom: 0;
      margin-bottom: 20px;
      width: 100%;
      padding-right: 40px;
    }
  }
}