@import '../../../utils/breakpoints';

.date {
  position: absolute;
  top: 0;
  right: 0;
  transform: translateY(50px);
  border: 1px solid rgba(26, 56, 96, 0.1);
  box-shadow: 0px 4px 15px rgba(189, 194, 199, 0.4);
  border-radius: 4px;
  width: 752px;
  z-index: 1000;
  background: white;
  padding: 30px 24px;
  &__header {
    display: none;
    justify-content: space-between;
    align-items: center;
  }
  &__title {
    font-size: 22px;
    line-height: 125.3%;  
  }
  &__blocks {
    column-gap: 30px;
    display: flex;
    justify-content: center;
  }
  &__block {
    width: 280px;
  }
  .block__date {
    border: 0;
    font-family: Helvetica, sans-serif;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: 0.12px;
    color: #192434;
  }
  .block__weekdays {
    margin-top: 10px;
    font-weight: bold;
    font-size: 10px;
    line-height: 16px;
    display: flex;
    align-items: center;
    text-align: center;
    column-gap: 8px;
    letter-spacing: 1.5px;
    text-transform: uppercase;
    color: rgba(27, 43, 65, 0.72);
  }
  .block__weekday {
    width: 32px;
  }
  .block__days {
    margin-top: 16px;
    display: flex;
    flex-wrap: wrap;
    column-gap: 8px;
  }
  .block__day {
    width: 32px;
    height: 32px;
    font-weight: 500;
    font-size: 14px;
    line-height: 22px;    
    display: flex;
    justify-content: center;
    align-items: center;
    letter-spacing: 0.22px;    
    color: rgba(24, 39, 58, 0.94);
    border-radius: 50%;
    &-this {
      background: rgba(30, 77, 252, 0.05);
    }
    &-active {
      background: rgba(30, 77, 252, 0.5);
    }
    &:hover {
      background: rgba(30, 77, 252, 0.3);
      color: #1E4DFC;
    }
  }
  &__buttons {
    margin-top: 10px;
    display: flex;
    justify-content: end;
    column-gap: 20px;
  }
  &__btn {
    transition: all 0.5s;
    &:hover {
      transform: scale(1.02);
    }
    width: 149px;
    &-cancel {
      padding: 0;
      font-size: 16px;
      color: rgba(28, 52, 84, 0.26);
      border: 1px solid rgba(28, 52, 84, 0.26);
      background: white;
      &:hover {
        border: 1px solid red;
        color: red;
      }
      &:disabled {
        color: red;
        opacity: 0.4;
      }
    }
    &:disabled {
      transform: scale(1.00);
    }
  }
}

@media(max-width: '900px') {
  .date {
    padding: 10px;
    width: 700px;
    right: -80px;
  }
}
@media(max-width: $mobile-width) {
  .date {
    right: 0;
    padding: 20px;
    padding-top: 40px;
    position: fixed;
    transform: translateY(0);
    width: 100%;
    height: 100%;
    border-radius: 0;
    align-items: center;
    &__arrow {
     display: none;
    }
    &__header {
      display: flex;
    }
    &__blocks {
      margin-top: 10px;
      display: flex;
      flex-direction: column;
      row-gap: 20px;
    }
    &__buttons {
      justify-content: start;
      flex-direction: column-reverse;
      row-gap: 10px;
    }
    &__btn {
      width: 280px;
    }
  }
}